import {
  PaginationType as AccountStatementPaginationType,
  SortDirection as AccountStatementSortDirection,
} from '@backbase/account-statement-common-ang';
import {
  EndpointType,
  PaginationType as TransactionPaginationType,
  TransactionsFilterOptions,
  TransactionsSortOptions,
} from '@backbase/transactions-common-ang';
import { LoanViewType } from '@backbase/loans-collection-ui-ang';

export interface LoansJourneyTransactionConfig {
  apiKey: string;
  maxNavPages: number;
  itemsPerPage: number;
  showChangeCategory: boolean;
  showManageNotes: boolean;
  showCheckImages: boolean;
  showPendingOnTop: boolean;
  showControls: boolean;
  initialFilterOptions: TransactionsFilterOptions;
  initialSortOptions: TransactionsSortOptions;
  endpointType: EndpointType;
  paginationType: TransactionPaginationType;
  refreshEvents: string;
  enableDisputeAndInquiry: boolean;
  disputeEligibilityDays: number;
  disputeTransactionTypes: string;
  disputeByBillingStatus: boolean;
  disputeTopicId: string;
  inquireTopicId: string;
}

export interface LoansJourneyAccountStatementConfig {
  itemsPerPage: number;
  sortingOrder: string;
  sortingDirection: AccountStatementSortDirection;
  notificationDismissTime: number;
  relativeDateLimit: number;
  absoluteDateLimit: string;
  paginationType: AccountStatementPaginationType;
}

export interface LoansJourneyLoansListConfig {
  itemsPerPage: number;
  maxNavPages: number;
}

export type LoansJourneyLoansPaymentScheduleConfig = LoansJourneyLoansListConfig;

export type LoansJourneyLoanTypeMappingConfig = Record<string, { viewType: LoanViewType; typeAlias?: string }>;

export interface LoansJourneyConfiguration {
  transaction: Partial<LoansJourneyTransactionConfig>;
  accountStatement: Partial<LoansJourneyAccountStatementConfig>;
  loanTypeMapping: LoansJourneyLoanTypeMappingConfig;
  loansList: LoansJourneyLoansListConfig;
  paymentSchedule: LoansJourneyLoansPaymentScheduleConfig;
}
